.hamburger {
  position: absolute;
  top: 40px;
  right: 30px;
  bottom: auto;
  left: auto;
  z-index: 10000;
  padding: 0;
  margin: 0;
}

.hamburger,.hamburger .menu-toggle {
  border: none;
  background: none;
}

.hamburger .menu-toggle {
  width: 40px;
  height: 22px;
  display: block;
  position: relative;
}

.hamburger .menu-toggle span {
  margin-top: -1px;
  text-indent: -9999em;
  top: 50%;
}

.hamburger .menu-toggle span,.hamburger .menu-toggle:before,.hamburger .menu-toggle:after {
  width: 40px;
  display: block;
  position: absolute;
  height: 2px;
  background: #fff;
  transition: transform .2s linear 0s, top .2s linear 0s, bottom .2s linear 0s, opacity .1s linear 0s;
}

.hamburger .menu-toggle:before,.hamburger .menu-toggle:after {
  content: "";
  left: 0;
  text-indent: -9999px;
  top: 2px;
  transform-origin: 50%;
  transform: translateX(0);
}

.hamburger .menu-toggle:after {
  bottom: 2px;
  left: auto;
  right: 0;
  top: auto;
}

.hamburger .menu-toggle:hover:before {
  top: 0;
}

.hamburger .menu-toggle:hover:after {
  bottom: 0;
}

.hamburger .menu-toggle.close:before,.hamburger .menu-toggle.close:after {
  width: 40px;
  top: 10px;
}

.hamburger .menu-toggle.close:before {
  transform: rotate(35deg);
}

.hamburger .menu-toggle.close span {
  opacity: 0;
}

.hamburger .menu-toggle.close:after {
  transform: rotate(-35deg);
}

.hamburger .menu-toggle.close span,.hamburger .menu-toggle.close:before,.hamburger .menu-toggle.close:after {
  background: #fff !important;
}

