.post-single .post-header.header-single .post-title,.post-page .post-header.header-single .post-title {
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
  opacity: 0;
  transform: translateY(10%);
  animation-name: animateFadeIn;
  animation-duration: 600ms;
  animation-delay: .15s;
}

.post-single .post-header.header-single.has-image,.post-page .post-header.header-single.has-image {
  background-color: #3a3a3b;
}

.post-single .post-header.header-single.has-image .header-overlay,.post-page .post-header.header-single.has-image .header-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  opacity: .5;
  background-position: 50% 0;
  background-size: cover;
  -webkit-filter: blur(4px);
  -moz-filter: blur(4px);
  -o-filter: blur(4px);
  -ms-filter: blur(4px);
  filter: blur(4px);
}

.post-article {
  margin-bottom: 60px;
}

.post-article .post-title {
  font-size: 1rem;
}

.post-article .post-title a {
  color: #79797b;
}

.post-article .post-title a:hover {
  color: #67a6c0;
}

.post-article p {
  opacity: .6;
  font-size: 0.88889rem;
}

.post-title {
  margin: 0;
}

.post-title a::after {
  content: "";
  background: transparent url(/assets/images/arrow.svg) no-repeat;
  background-size: 14px;
  height: 22px;
  width: 14px;
  display: inline-block;
  left: 5px;
  opacity: 0;
  position: relative;
  top: 0;
  transition: all .15s ease-in-out;
}

.post-title a:hover::after {
  left: 15px;
  opacity: 1;
}

.post-meta {
  opacity: .6;
  display: block;
  margin-bottom: 10px;
  font-size: 0.77778rem;
}

.post-meta .label {
  display: inline-block;
  padding: 2px 5px;
  background-color: #f9f2e7;
  color: #212121;
  text-transform: uppercase;
  font-weight: 500;
  margin-left: 5px;
  font-size: 0.66667rem;
}

.disqus {
  background-color: #f9f2e7;
}

.social {
  padding: 0;
}

.social li {
  font-size: 1.55556rem;
  display: inline-block;
  padding: 15px;
  transition: transform .2s ease-in-out;
}

.social li:hover {
  transform: scale(1.1);
}

.video-container {
  margin: 15px 0;
  text-align: right;
}

.video-responsive {
  margin: 0;
  position: relative;
  padding-bottom: 56.25%;
 /* 16:9 */
  padding-top: 25px;
  height: 0;
}

.video-responsive iframe,.video-responsive object,.video-responsive embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
  padding: 0;
  margin: 0;
}

body.home-hero {
  background-color: #fff;
  padding: 10px;
}

.hero {
  width: 100%;
  text-align: center;
  background-color: #40809a;
  margin-bottom: 10px;
}

.hero::after {
  clear: both;
  content: "";
  display: table;
}

.hero h1,.hero .actions,.hero .social {
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.25, 0.1, 0.25, 1);
}

.hero .hero-inner {
  max-width: 1024px;
}

.hero .actions,.hero .avatar {
  margin: 15px 0;
}

.hero a {
  color: #fff;
  opacity: .7;
  transition: opacity .2s ease-in-out;
}

.hero a:hover,.hero a:active,.hero a:focus {
  opacity: 1;
}

.hero .actions {
  opacity: 0;
  transform: translateY(100%);
  animation-name: translateBottomIn;
  animation-duration: 600ms;
  animation-delay: 1.2s;
  margin-top: 30px;
}

.hero .actions a {
  border-radius: 150px;
  margin: 0 5px;
  text-transform: uppercase;
}

.hero h1,.hero .social {
  opacity: 0;
  transform: translateY(10%);
  animation-name: animateFadeIn;
}

.hero h1 {
  animation-duration: 900ms;
  animation-delay: .5s;
  color: #fff;
  line-height: 1;
  margin: 0;
  padding: 0;
}

.hero h1 span {
  opacity: .7;
  font-family: "Raleway", Helvetica, Arial, sans-serif;
  font-weight: 300;
  font-size: 1rem;
}

.hero h1 em {
  font-style: normal;
}

.hero h1 em::after {
  background-color: #fff;
}

.hero h1 strong {
  display: block;
  margin: 15px 0;
  text-transform: uppercase;
  letter-spacing: .1em;
  font-size: 1.22222rem;
}

.hero h1 strong::before,.hero h1 strong::after {
  opacity: .7;
  content: "";
  display: block;
  border: none;
  background-color: #fff;
  width: 100%;
  height: 2px;
  -webkit-transition: opacity 0.2s ease-in-out;
  -moz-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
}

.hero h1 strong:hover::before,.hero h1 strong:hover::after,.hero h1 strong:focus::before,.hero h1 strong:focus::after {
  opacity: .75;
}

.hero .social {
  animation-duration: 800ms;
  animation-delay: 1.8s;
  margin: 30px 0 0;
}

.hero .social li {
  padding-top: 0;
  padding-bottom: 0;
}

.pagination {
  margin: 15px auto;
}

.pagination ul {
  list-style: none;
  padding: 0;
}

.pagination .next,.pagination .prev,.pagination .page-number {
  padding: 15px 30px;
  font-size: 0.88889rem;
  display: inline-block;
}

.pagination .page-number {
  color: rgba(58, 58, 59, 0.5);
  font-size: 0.88889rem;
}

.pagination .next,.pagination .prev {
  color: #40809a;
  border-color: #40809a;
  border-radius: 100px;
}

.pagination .next:hover,.pagination .next:active,.pagination .next:focus,.pagination .prev:hover,.pagination .prev:active,.pagination .prev:focus {
  color: #67a6c0;
  border-color: #67a6c0;
}

.pagination li {
  display: inline-block;
}

.lightbox {
  cursor: pointer;
}

.lightbox-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.95);
  overflow: hidden;
  z-index: 9999999;
}

.lightbox-exit,.lightbox-button-left,.lightbox-button-right {
  position: absolute;
  width: 20px;
  height: 20px;
  background: url("../images/icons.svg");
  background-size: 60px 20px;
  z-index: 99999999999;
  cursor: pointer;
}

.lightbox-exit {
  top: 25px;
  right: 25px;
  background-position: 0 0;
}

.lightbox-button-left,.lightbox-button-right {
  top: 50%;
  transform: translate(0, -50%);
}

.lightbox-button-left {
  left: 25px;
  background-position: 20px 0;
}

.lightbox-button-right {
  right: 25px;
  background-position: 40px 0;
}

.lightbox-inactive {
  opacity: 0.2;
}

.lightbox-slides {
  height: 100%;
}

.lightbox-slide {
  position: absolute;
  display: block;
  float: left;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

.lightbox-slide img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;
  max-height: 100vh;
}

.lightbox-caption {
  position: absolute;
  width: 100%;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  color: white;
  line-height: 50px;
  font-size: 20px;
  text-align: center;
}

.lightbox-loader {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999999999999;
  opacity: 0;
}

svg path,svg rect {
  fill: #fff;
}

.lightbox-slides {
  transition: 500ms;
}

.footer {
  text-align: center;
  padding: 30px 0;
  border-top: 1px solid #eaeaea;
}

.footer p {
  color: rgba(58, 58, 59, 0.5);
  font-size: 0.77778rem;
}

