@media screen and (min-width: 375px) {
  .hero h1 strong {
    letter-spacing: .2em;
    font-size: 2.11111rem;
  }
}

@media screen and (min-width: 768px) {
  .container,.container-inn {
    padding: 20px;
  }

  .flex-row .post {
    flex-basis: 50%;
    max-width: 50%;
  }

  .post-article .post-title {
    font-size: 1.22222rem;
  }

  body.home-hero {
    padding: 15px;
  }

  .hero {
    margin-bottom: 15px;
  }

  .hero h1 {
    line-height: 1.6;
  }

  .hero h1 span {
    font-size: 1rem;
  }

  .hero h1 strong {
    font-size: 2.77778rem;
  }

  .pagination ul {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
  }
}

@media screen and (min-width: 1024px) {
  .container,.container-inn {
    padding: 30px;
  }

  .post-header .post-title small {
    width: 80%;
  }

  .post-single .post-header.header-single,.post-page .post-header.header-single {
    padding: 4.44444em 3.33333em;
  }

  body.home-hero {
    overflow: hidden;
    padding: 20px;
  }

  .hero {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }

  .hero h1 span {
    font-size: 1.77778rem;
  }

  .hero h1 strong {
    font-size: 4.16667rem;
  }
}

@media screen and (max-width: 1024px) {
  .hero .hero-inner {
    padding: 15px;
  }
}

@media screen and (max-width: 768px) {
  .avatar {
    width: 100px;
    height: 100px;
  }

  .header {
    padding-bottom: 10px;
  }

  .header .container {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .logotipo {
    line-height: 30px;
  }

  .logotipo a {
    line-height: 33px;
  }

  .navbar li {
    margin-left: 0;
    margin-bottom: 15px;
  }

  .navbar li,.navbar li a {
    width: 100%;
    display: block;
  }

  .navbar li a {
    padding: 10px 0;
  }

  .pagination li,.pagination .next,.pagination .prev,.pagination .page-number {
    text-align: center;
    display: block;
    width: 100%;
  }
}

@media screen and (max-width: 320px) {
  .logotipo span {
    display: none;
    visibility: hidden;
  }
}

